/* ==========================================================================
   Error
   ========================================================================== */
html {
  height: 100%;
}
body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.page-error-box {
  background: #fff;
  border: solid 1px #d8e2e7;
  border-radius: 5px;
  padding: 50px 30px 55px;
  text-align: center;
  margin: 0 auto;
  width: 100%;
  max-width: 475px;
  color: #919fa9;
  line-height: 1;
}
.page-error-box .error-code {
  font-size: 9.375rem /*150/16*/;
  font-weight: 600;
}
.page-error-box .error-title {
  font-size: 2.25rem /*36/16*/;
  font-weight: 600;
  margin: 0 0 1.5rem /*24/16*/;
}
@media (max-width: 767px) {
  .page-error-box {
    padding: 25px 15px;
  }
  .page-error-box .error-code {
    font-size: 5.5rem;
  }
  .page-error-box .error-title {
    font-size: 1.5rem;
  }
}
